import React from 'react';
import { Button, Modal, Card } from 'antd';

const DataManager = () => {
    const levelData = localStorage.getItem('levelData');

    if (!levelData || !isValidJson(levelData)) {
        return (
            <Card
            title="Data Management"
            style={{
                margin: '24px 0',
            }}
            bordered={true}
        >
            <div>Level data not present or not valid, please load your Level.sav.json file</div>
        </Card>            
        )
    }

    const handleExport = () => {
        // TODO: Implement export function

    };

    const handleImport = () => {
        // TODO: Implement import function
    };

    const handleClear = () => {
        localStorage.removeItem('levelData');
        Modal.confirm({
            title: 'Clear Data',
            content: 'Are you sure you want to clear the level data?',
            onOk: () => {
                // TODO: Show confirmation modal
            },
        });
    };

    return (

        <Card
            title="Data Management"
            style={{
                margin: '24px 0',
            }}
            bordered={true}
        >
            <Button onClick={handleExport}>Export data</Button>
            <Button onClick={handleImport}>Import data</Button>
            <Button onClick={handleClear}>Clear data</Button>
        </Card>
    );
};

const isValidJson = (jsonString) => {
    try {
        JSON.parse(jsonString);
        return true;
    } catch (error) {
        return false;
    }
};

export default DataManager;