import React, { useState } from 'react';
import { Upload, message } from 'antd';
import { UploadOutlined } from '@ant-design/icons';

import axios from 'axios';

const { Dragger } = Upload;

const FileUploader = () => {
    const [fileList, setFileList] = useState([]);


    const onChange = ({ fileList: newFileList }) => {
        setFileList(newFileList);
    };
    
    const onRemove = (file) => {
        const index = fileList.indexOf(file);
        const newFileList = fileList.slice();
        newFileList.splice(index, 1);
        setFileList(newFileList);
    };

    const customRequest = async({ file, onSuccess, onError }) => {
        try{
            const formData = new FormData();
            formData.append('file', file);
            const response = await axios.post('http://localhost:3501/upload', formData)
            .then(response => {
                if (response.status === 200 && response.data.filesave) {
                    localStorage.setItem('levelData', response.data.filesave);
                    message.success('File uploaded successfully!');
                    console.log("Success")
                    onSuccess();

                } else {
                    console.log("Error 1");
                    message.error('Failed to upload file.');
                    onError();
                }
            })
            .catch(error => {
                console.log("Error 2");
                message.error('Failed to upload file.');
                onError();
            });
        } catch (error) {
            console.log("Error 3");
            message.error('Failed to upload file.');
            onError();
        }



    };
    const props = {
        customRequest,
        onChange,
        onRemove,
        fileList,
        listType: "text",
    };
    return (
        <Dragger
            style={{
                maxHeight: '200px',
                maxWidth: '400px',
                alignSelf: 'center',
                marginLeft: 'auto',
                marginRight: 'auto',
            }}
            {...props}
        >
            <p className="ant-upload-drag-icon">
                {/* Add your drag and drop icon here */}
            </p>
            <p className="ant-upload-text">Click or drag file to this area to upload</p>
            <p className="ant-upload-hint">Only support Level.sav.json file</p>
        </Dragger>
    );
};

export default FileUploader;